<template>
     <div class="navbar sticky" :class="{ active: menu }" id="navbar">
    <div class="logo">
      <a class="search-button mt-2 responsive" v-if="searchButton" @click="isSearch" ><i class="fa-solid fa-magnifying-glass"></i
      ></a>
      <h1 class="navbar-brand" :class="{ hidden: search }"><a>Watch Cinemas</a></h1>
      <button class="navbar_open" @click="menu_toggle" v-if="searchButton">
        <i class="fa-solid fa-bars"></i>
      </button>
    </div>
    <div class="navbar_items" :class="{ hidden: search }">
      <ul id="navbar_items">
        <li><router-link to="/">Home</router-link ></li>
        <li><router-link to="/Anime">Anime</router-link ></li>
        <li><router-link to="/Movies">Movies</router-link ></li>
        <li><router-link to="/Series">Series</router-link ></li>
      </ul>
    </div>
    <a class="search-button mt-2 non-responsive " :class="{hidden: search }" @click="isSearch" ><i class="fa-solid fa-magnifying-glass"></i></a>
    <div class="search_container " :class="{ hidden: !search }">
      <input type="text" placeholder="Search.." />
      <a @click="isSearch"><i class="fa-solid fa-x"></i></a>
    </div>
  </div>

</template>
<script>
import { ref,onMounted,onBeforeUnmount } from 'vue';


export default{
    name:"header_page",
    setup() {
    const search = ref(false);
    const searchButton = ref(true);
    const menu = ref(false);
    const screenWidth = ref(window.innerWidth); // Initialize screenWidth with the initial window width
    
    const updateScreenWidth = () => {
        screenWidth.value = window.innerWidth; // Update screenWidth when window is resized
    };

    onMounted(() => {
      window.addEventListener('resize', updateScreenWidth); // Add event listener for window resize
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', updateScreenWidth); // Remove event listener when component is unmounted
    });
 

    const isSearch = () => {

        search.value = !search.value;
        if(700 >= screenWidth.value){
            searchButton.value = !searchButton.value
            menu.value = false;
        }
    };
    
    const menu_toggle = () => {
      menu.value = !menu.value;
    };

    return {


      isSearch,
      search,
      menu,
      menu_toggle,
      searchButton
    };
}
}

</script>
<style scoped>
.responsive {
  display: none;
}
.search-button {
  color: white;
  cursor: pointer;
}
.search_container {
  width: 100%;
  position: relative;
}
.search_container input {
  width: 100%;
  height: 45px;
  border-radius: 10px;
  border: none;
  padding: 0 2.5rem 0 1rem;
  outline: none;
  background-color: rgba(105, 109, 109, 0.3);
  color: white;
}
.search_container a {
  position: absolute;
  top: 0;
  right: 0;
  height: 50px;
  width: 2.5rem;
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.search_container a i {
  color: red;
}
.hidden {
  display: none;
}
.navbar {
  position: fixed;
  height: 5rem;
  display: flex;
  left: -2rem;
  top: 0;
  width: calc(100vw + 2rem);
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem 0 2rem;
  z-index: 1000;
  transition: 1s;
}
.navbar.sticky {
  background-color: #000;
}
.navbar.hide {
  height: 0;
  transition: 0.2s;
  opacity: 0;
  overflow: hidden;
}
.navbar .logo {
  display: flex;
  padding-left: 2rem;
}

.navbar-brand {
  color: white;
}
.navbar .logo button {
  display: none;
  font-size: 1.5rem;
  background-color: transparent;
  outline: none;
  border: none;
  color: #fff;
}
.navbar .navbar_items ul {
  display: flex;
  list-style: none;
}
.navbar .navbar_items ul li {
  margin-right: 1.5rem;
}
.navbar .navbar_items ul li a {
  color: #fff;
  font-size: 1.1rem;
  position: relative;
  cursor: pointer;
  text-decoration: none;
}
.navbar .navbar_items ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: red;
  transform-origin: bottom left;
  transition: transform 0.25s ease-out;
  transform: scaleX(0);
}
.navbar .navbar_items ul li a:hover {
  color: red;
}
.navbar .navbar_items ul li a:hover::after {
  transform: scaleX(1);
}
@media (max-width: 700px) {
  .responsive {
    display: block;
  }
  .non-responsive{
    display: none;
  }
  .navbar {
    flex-direction: column;
    height: 4rem;
    justify-content: center;

    transition: all 0.5s ease-in;
    /* background-color: ; */
  }
  .navbar.active {
    height: 100vh;
    background-color: #000;
    justify-content: flex-start;
  }
  .navbar .logo button {
    display: block;
    cursor: pointer;
  }
  .navbar .logo {
    width: 100vw;
    display: flex;
    justify-content: space-between;
    padding: 0 1rem 0 1rem;
  }

  .navbar .navbar_items ul {
    width: 100vw;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    padding-top: 1rem;
    background-color: rgba(0, 0, 0 0.9);
    transition: all 1.5s ease-in;
  }

  .navbar .navbar_items ul.active {
    width: 100vw;
  }
  .navbar .navbar_items ul li {
    padding-top: 1.5rem;
    border-bottom: 1px solid #fff;
    /* padding-left: 40%; */
    padding-left: 2rem;
    margin-left: 1rem;
    padding-bottom: 1rem;
  }
  .navbar .navbar_items ul li:first-child {
    border-top: 1px solid #fff;
  }
}
</style>