<template>
    <headerVue/>
  <div class="p-4 " style="min-height: 100vh; margin-top: 4rem;" >
    <h3 class="text-white">{{ category_name }}</h3>
    <div class="row">
      <div class="col-md-8">
        <div class="row g-2">

            <movie_post_categoryVue v-for="movie in currentPageItems" :key="movie.id" :details="movie"/>
        </div>
        <div class="mt-4">
            <div class="d-flex justify-content-center">
                <ul class="custom-pagination">
                    <li><a class="pagination_items" :class="{ 'disabled': currentPage === 1 }" @click.prevent="prevPage"><i class="fa-solid fa-chevron-left"></i></a></li>
                    <li v-for="page in pageList" :key="page"> <a class="pagination_items" @click.prevent="changePage(page)">{{ page }}</a></li>  
                    <li><a class="pagination_items" :class="{ 'disabled': currentPage === totalPages }" @click.prevent="nextPage"><i class="fa-solid fa-chevron-right"></i></a></li>
                </ul>
            </div>
        </div>
      </div>
      <div class="col-md-4">
        <searchVue />
        <adsVue />
        <side_cardVue name="Genres" :details="genre" />
        <adsVue />
        <side_cardVue name="Category" :details="category" />
        <adsVue />
        <filter_cardVue />
      </div>
    </div>
</div>
<footerVue />
</template>
<script>
import { ref,computed,onMounted } from "vue";
import side_cardVue from './side_card.vue'
import filter_cardVue from './filter_card.vue'
import searchVue from './search.vue'
import adsVue from './ads.vue';
import footerVue from "./footer.vue";
import movie_post_categoryVue from './movie_post_category.vue';
import headerVue from './header.vue';
import { useRoute } from "vue-router";
export default {
  name: "movies_page",
  components:{side_cardVue,filter_cardVue,searchVue,adsVue,footerVue,movie_post_categoryVue,headerVue},
  setup() {
    const category_name = ref("Movies")
    const currentPage = ref(1);
    const itemsPerPage = 1;
    const router = useRoute();
    // const pagelist=[];
    
    const genre = ref([
      { name: "Action", count: 95 },
      { name: "Comedy", count: 120 },
      { name: "Drama", count: 68 },
      { name: "Horror", count: 135 },
      { name: "Sci-Fi", count: 105 },
    ]);
    const category = ref([
      { name: "Movies", count: 95 },
      { name: "Anime", count: 120 },
      { name: "Series", count: 68 },
    ]);
    const movies = ref([
    {
        id: 1,
        name: "Inception",
        image: "https://i.pinimg.com/736x/fa/20/85/fa2085a086003ebe5cccafeeeaf9952e.jpg",
        category: "Movie"
    },
    {
        id: 2,
        name: "Attack on Titan",
        image: "https://wallpapercave.com/wp/wp8326482.jpg",
        category: "Anime"
    },
    {
        id: 3,
        name: "Breaking Bad",
        image: "https://m.media-amazon.com/images/I/71HuWAZ3ydL._AC_UF1000,1000_QL80_.jpg",
        category: "Series"
    },
    {
        id: 4,
        name: "The Dark Knight",
        image: "https://w0.peakpx.com/wallpaper/578/765/HD-wallpaper-dark-knight-poster-batman-dark-knight.jpg",
        category: "Movie"
    },
    {
        id: 5,
        name: "Death Note",
        image: "https://cdn.europosters.eu/image/750/posters/death-note-duo-i28405.jpg",
        category: "Anime"
    },
    {
        id: 6,
        name: "Stranger Things",
        image: "https://i.pinimg.com/564x/9e/36/74/9e36748a38c77f161744bfb95a6de8f4.jpg",
        category: "Series"
    },
    {
        id: 7,
        name: "Interstellar",
        image: "https://rukminim2.flixcart.com/image/850/1000/juk4gi80/poster/s/g/3/large-newposter8503-movie-interstellar-hd-wallpaper-background-original-imaf5ttsfnfgt2zh.jpeg?q=90&crop=false",
        category: "Movie"
    },
    {
        id: 8,
        name: "Naruto",
        image: "https://images.meesho.com/images/products/163097226/nj9ot_512.webp",
        category: "Anime"
    },
    {
        id: 9,
        name: "Game of Thrones",
        image: "https://i.pinimg.com/736x/ae/8c/21/ae8c21bc78b35923cdd54ef5868915ef.jpg",
        category: "Series"
    },
    {
        id: 10,
        name: "The Godfather",
        image: "https://m.media-amazon.com/images/I/71khjV-MoOS._AC_UF894,1000_QL80_.jpg",
        category: "Movie"
    },
    // Add more media objects as needed
]);
const totalPages = computed(() => Math.ceil(movies.value.length / itemsPerPage));
const pageList = computed(() => {
      const list = [];
      for (let i = 1; i <= totalPages.value; i++) {
        list.push(i);
      }
      return list;
    });

const currentPageItems = computed(() => {
  const startIndex = (currentPage.value - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  return movies.value.slice(startIndex, endIndex);
});
const changePage = (pageNumber) => {
      currentPage.value = pageNumber;
    };

    const nextPage = () => {
      if (currentPage.value < totalPages.value) {
        currentPage.value++;
      }
    };

    const prevPage = () => {
      if (currentPage.value > 1) {
        currentPage.value--;
      }
    };
    onMounted(()=>{
      category_name.value = router.params.category
    })
    return { genre, category,movies,  currentPage,currentPageItems,totalPages,prevPage,changePage,nextPage,pageList,category_name };
  },
};
</script>
<style scoped>
.custom-pagination{
    display: flex;
    flex-wrap: wrap;
    gap:10px;
    list-style: none;
 
}

.pagination_items.disabled{
    pointer-events: none;
    color: #999191;
}

.pagination_items{
    padding: 5px 10px;
    color: white;
    text-decoration: none;
    border-radius: 5px;
    /* margin: 5px 5px 0 0;  */
    

    background: rgba(95, 92, 92, 0.5);
    cursor: pointer;
}
.pagination_items:hover{
    background: red;
}
.pagination_items.active{
    background: red;
    

}

</style>