<template>

  <div class="navbar" :class="{ sticky: scrolled, active: menu }" id="navbar">
    <div class="logo">
      <a class="search-button mt-2 responsive" v-if="searchButton" @click="isSearch" ><i class="fa-solid fa-magnifying-glass"></i
      ></a>

      <h1 class="navbar-brand" :class="{ hidden: search }"><a>Watch Cinemas</a></h1>
      <button class="navbar_open" @click="menu_toggle" v-if="searchButton">
        <i class="fa-solid fa-bars"></i>
      </button>
    </div>
    <div class="navbar_items" :class="{ hidden: search }">
      <ul id="navbar_items">
        <li><router-link to="/">Home</router-link ></li>
        <li><router-link to="/Anime">Anime</router-link ></li>
        <li><router-link to="/Movies">Movies</router-link ></li>
        <li><router-link to="/Series">Series</router-link ></li>
      </ul>
    </div>
    <a class="search-button mt-2 " :class="{hidden: search }" @click="isSearch" ><i class="fa-solid fa-magnifying-glass"></i></a>
    <div class="search_container " :class="{ hidden: !search }">
      <input type="text" placeholder="Search.." />
      <a @click="isSearch"><i class="fa-solid fa-x"></i></a>
    </div>
  </div>

  <!-- Carousel -->
  <div
    id="carouselExampleSlidesOnly"
    class="carousel slide"
    data-ride="carousel"
  >
    <div class="carousel-inner">
      <div
        class="carousel-item"
        v-for="(movie, index) in movies"
        :key="movie.id"
        :class="{ active: active(index + 1) }"
      >
        <img
          class="d-block w-100 h-100"
          :src="movie.image"
          alt="First slide"
          style="object-fit: cover; object-position: center"
        />

        <div class="carousel-caption d-flex flex-column justify-content-center">
          <div class="col-md-6">
            <div class="d-flex">
              <p class="misc">{{ movie.category }}</p>
              <p class="ms-2 misc">></p>
              <p class="misc ms-2">{{ movie.genre }}</p>
            </div>
            <h1 class="d-flex">
              {{ movie.name }}
            </h1>
            <p class="desc">{{ movie.desc }}</p>
            <p class="misc">Rating: {{ movie.rating }}</p>
            <button class="custom_button d-flex align-items-center">
              <i class="fa-solid fa-play"></i>Watch Now
            </button>
          </div>
        </div>
      </div>
    </div>
    <a
      class="carousel-control-prev"
      role="button"
      data-slide="prev"
      @click="prev_slide"
    >
      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
    </a>
    <a
      class="carousel-control-next"
      role="button"
      data-slide="next"
      @click="next_slide"
    >
      <span class="carousel-control-next-icon" aria-hidden="true"></span>
    </a>
  </div>
</template>
<script>
import { ref, onMounted, onUnmounted } from "vue";

export default {
  name: "header_hero",
  setup() {
    const currentIndex = ref(1);
    const search = ref(false);
    const menu = ref(false);
    const searchButton = ref(true);
    const scrolled = ref(false);
    const yaxis = ref(0);
    const screenWidth = ref(window.innerWidth); // Initialize screenWidth with the initial window width
    
    const updateScreenWidth = () => {
        screenWidth.value = window.innerWidth; // Update screenWidth when window is resized
    };
    const movies = ref([
      {
        id: 1,
        name: "Inception",
        desc: "A thief who steals corporate secrets through the use of dream-sharing technology is given the inverse task of planting an idea into the mind of a C.E.O.",
        genre: "Action, Adventure, Sci-Fi",
        category: "Movie",
        rating: 8.8,
        image:
          "https://upload.wikimedia.org/wikipedia/en/2/2e/Inception_%282010%29_theatrical_poster.jpg",
      },
      {
        id: 2,
        name: "The Dark Knight",
        desc: "When the menace known as the Joker wreaks havoc and chaos on the people of Gotham, Batman must accept one of the greatest psychological and physical tests of his ability to fight injustice.",
        genre: "Action, Crime, Drama",
        category: "Movie",
        rating: 9.0,
        image:
          "https://w0.peakpx.com/wallpaper/578/765/HD-wallpaper-dark-knight-poster-batman-dark-knight.jpg",
      },
      {
        id: 3,
        name: "Interstellar",
        desc: "A team of explorers travel through a wormhole in space in an attempt to ensure humanity's survival.",
        genre: "Adventure, Drama, Sci-Fi",
        category: "Movie",
        rating: 8.6,
        image:
          "https://upload.wikimedia.org/wikipedia/en/b/bc/Interstellar_film_poster.jpg",
      },
    ]);

    const active = (index) => {
      if (currentIndex.value == index) {
        return true;
      }
      return false;
    };
    onMounted(() => {
      setInterval(() => {
        next_slide();
      }, 5000);
    });
    const next_slide = () => {
      if (movies.value.length > currentIndex.value) {
        currentIndex.value += 1;
      } else {
        currentIndex.value = 1;
      }
    };
    const prev_slide = () => {
      if (currentIndex.value > 1) {
        currentIndex.value -= 1;
      } else {
        currentIndex.value = movies.value.length;
      }
    };
    const handleScroll = () => {
      yaxis.value = window.scrollY
      if (search.value == false) {
        scrolled.value = window.scrollY > 0;
      } else {
        scrolled.value = window.scrollY >= 0;
      }
    };

    onMounted(() => {
      window.addEventListener("scroll", handleScroll);
      window.addEventListener('resize', updateScreenWidth); // Add event listener for window resize
    });

    onUnmounted(() => {
      window.removeEventListener("scroll", handleScroll);
      window.removeEventListener('resize', updateScreenWidth); // Add event listener for window resize
    });
    const isSearch = () => {
      if (search.value == false) {
        search.value = true;
        scrolled.value = true;
      } else {
        search.value = false;
        if (yaxis.value> 0) {
          scrolled.value = true;
       
        } else {
          scrolled.value = false;
        }
      }
      if(700 >= screenWidth.value){
            searchButton.value = !searchButton.value
            menu.value = false;
        }
    };
    const menu_toggle = () => {
      menu.value = !menu.value;
    };
    return {
      active,
      currentIndex,
      movies,
      next_slide,
      prev_slide,
      isSearch,
      search,
      scrolled,
      menu,
      menu_toggle,
      searchButton
    };
  },
};
</script>
<style scoped>
.responsive {
  display: none;
}
.navbar {
  position: fixed;
  height: 5rem;
  display: flex;
  left: -2rem;
  top: 0;
  width: calc(100vw + 2rem);
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem 0 2rem;
  z-index: 1000;
  transition: 1s;
}
.navbar.sticky {
  background-color: #000;
}
.navbar.hide {
  height: 0;
  transition: 0.2s;
  opacity: 0;
  overflow: hidden;
}
.navbar .logo {
  display: flex;
  padding-left: 2rem;
}

.navbar-brand {
  color: white;
}
.navbar .logo button {
  display: none;
  font-size: 1.5rem;
  background-color: transparent;
  outline: none;
  border: none;
  color: #fff;
}
.navbar .navbar_items ul {
  display: flex;
  list-style: none;
}
.navbar .navbar_items ul li {
  margin-right: 1.5rem;
}
.navbar .navbar_items ul li a {
  color: #fff;
  font-size: 1.1rem;
  position: relative;
  cursor: pointer;
  text-decoration: none;
}
.navbar .navbar_items ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: red;
  transform-origin: bottom left;
  transition: transform 0.25s ease-out;
  transform: scaleX(0);
}
.navbar .navbar_items ul li a:hover {
  color: red;
}
.navbar .navbar_items ul li a:hover::after {
  transform: scaleX(1);
}
.carousel-item {
  height: 100vh;
}
.carousel-caption {
  position: absolute;
  top: 10%;
  left: 5%;
  /* transform: translate(-50%, -50%); */
  text-align: center;
  color: white;
}
.carousel-control-next,
.carousel-control-prev {
  width: auto;
}
.carousel-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6); /* Semi-transparent black overlay */
}

.desc {
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Number of lines to show */
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-size: 0.9rem;
  font-weight: 300;
  text-align: left;
}
.custom_button {
  background: red;
  color: white;
  border: none;
  padding: 8px 10px;
  border-radius: 5px;
  border: 2px solid red;
  transition: 0.2s;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.custom_button i {
  margin-right: 8px;
}
.custom_button:hover {
  background: transparent;
  color: red;
}
.misc {
  text-align: left;
  font-size: 0.9rem;
  font-weight: 300;
  color: rgb(184, 196, 196);
}
.search-button {
  color: white;
  cursor: pointer;
}
.search_container {
  width: 100%;
  position: relative;
}
.search_container input {
  width: 100%;
  height: 45px;
  border-radius: 10px;
  border: none;
  padding: 0 2.5rem 0 1rem;
  outline: none;
  background-color: rgba(105, 109, 109, 0.3);
  color: white;
}
.search_container a {
  position: absolute;
  top: 0;
  right: 0;
  height: 50px;
  width: 2.5rem;
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.search_container a i {
  color: red;
}
.hidden {
  display: none;
}

@media (max-width: 700px) {
  .responsive {
    display: block;
  }
  .navbar {
    flex-direction: column;
    height: 4rem;
    justify-content: center;

    transition: all 0.5s ease-in;
    /* background-color: ; */
  }
  .navbar.active {
    height: 100vh;
    background-color: #000;
    justify-content: flex-start;
  }
  .navbar .logo button {
    display: block;
    cursor: pointer;
  }
  .navbar .logo {
    width: 100vw;
    display: flex;
    justify-content: space-between;
    padding: 0 1rem 0 1rem;
  }

  .navbar .navbar_items ul {
    width: 100vw;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    padding-top: 1rem;
    background-color: rgba(0, 0, 0 0.9);
    transition: all 1.5s ease-in;
  }

  .navbar .navbar_items ul.active {
    width: 100vw;
  }
  .navbar .navbar_items ul li {
    padding-top: 1.5rem;
    border-bottom: 1px solid #fff;
    /* padding-left: 40%; */
    padding-left: 2rem;
    margin-left: 1rem;
    padding-bottom: 1rem;
  }
  .navbar .navbar_items ul li:first-child {
    border-top: 1px solid #fff;
  }
}
</style>
